<template>
    <div style="margin: 200px;">
        <u-tooltip always placement="top-end" :content="text" :delay="1000" theme="light">
            <u-button @on-click="disabled = true">延时1秒显示</u-button>
        </u-tooltip>
        <u-tooltip :max-width="200" content="我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长">
            <u-button @on-click="handleChange">change</u-button>
        </u-tooltip>
        <u-poptip title="Title" padding="0" width="250" word-wrap content="我的文本超级，无敌,长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长我的文本超级无敌长">
            <u-tooltip placement="top" :content="text" :delay="500" theme="light">
                <u-button>Click</u-button>
            </u-tooltip>
        </u-poptip>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                text: 'u-tooltip 文字提示'
            };
        },
        methods: {
            handleChange () {
                this.text = '提示';
            }
        }
    };
</script>



